body{
  font-size: 15px;
  font-family: 'Overpass', sans-serif;
  box-sizing:border-box
}

.App{
  background-color: hsl(216, 12%, 8%);
  width:100vw;
  height:100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rating{
  width: 30%;
  height: 40%;
  background-color:hsl(213, 19%, 18%);
  border-radius: 10%;
  padding: 2%;
  padding-bottom: 1%;
}
.circle{
  background-color: hsl(204, 4%, 22%);
  border-radius: 50%;
  padding: 4%;
  width: 5%;
  margin-bottom: 2%;
}
h1{
  color:hsl(0, 0%, 100%);
  text-align: left;
  font-weight: 700;
}
p{
  padding: 1%;
  line-height: 2rem;
  color: hsl(217, 12%, 63%);
  text-align: left;
  font-weight: 400;
  font-size: larger;

}
.num{
  display:inline-block;
  margin: 3%;
  color:hsl(0, 0%, 100%);
  cursor: pointer;
}
.submit-button{
  background-color: hsl(25, 97%, 53%);
  border-radius: 40px;
  margin: 7%;
  border: none;
  width: 90%;
  padding: 4%;
  cursor: pointer;
}
button{
  color: hsl(0, 0%, 100%);
  font-size: large;
  margin-top: 7%;
}

.rating-para{
  border: none;
  border-radius: 40px;
  background-color: hsl(204, 4%, 22%);
  text-align: center;
  color: hsl(25, 97%, 53%);
  width: 60%;
  align-self: center;
  padding: 1%;
  margin: 2%;
}
.submit-button:hover{
  color:hsl(25, 97%, 53%);
  background-color: hsl(0, 0%, 100%); ;
}


.thank-you-image{
  height: 60%;
  width: 50%;
  margin: 5%;
}
.thank-you{
  text-align: center;
  font-weight: 700;
}
.appreciation-para{
  text-align: center;
  font-weight: 400;
}
@media screen and (max-width: 1060px) {
  .rating{
    width:40%;
  }
}
@media screen and (max-width: 800px) {
  .rating{
    width:50%;
    height: 60%;
  }
}
@media screen and (max-width: 600px) {
  .rating{
    width:70%;
  }
}
@media screen and (max-width: 450px) {
  .rating-para{
    width:70%;
  }
}
@media screen and (min-width: 1000px) and (max-height: 800px) {
  .rating{
    width:40%;
    height: 70%;
  }
}
  


